<template>
	<div class="max-w-screen-sm mx-auto px-4 py-10">
		<Loading class="loading-icon" v-model:active="isLoading" />
		<!-- App Message -->
		<div v-if="statusMsg || errorMsg" class="mb-10 p-4 rounded-md shadow-md bg-light-grey">
			<p class="text-at-light-green">
				{{ statusMsg }}
			</p>
			<p class="text-red-500">
				{{ errorMsg }}
			</p>
		</div>
		<div v-if="!isLoading">
			<!-- Workout Info -->
			<div class="flex flex-col items-center p-8 rounded-md shadow-md bg-light-grey relative">
				<div v-if="user" class="flex absolute left-2 top-2 gap-x-2">
					<div @click="editMode" class="h-7 w-7 rounded-full flex justify-center items-center cursor-pointer bg-at-light-green shadow-lg">
						<img class="h-3.5 w-auto" src="@/assets/images/pencil-light.png" alt="" />
					</div>
					<div @click="deleteWorkout" class="h-7 w-7 rounded-full flex justify-center items-center cursor-pointer bg-at-light-green shadow-lg">
						<img class="h-3.5 w-auto" src="@/assets/images/trash-light.png" alt="" />
					</div>
				</div>
				<img v-if="data.workout_type === 'cardio'" class="h-24 w-auto" src="@/assets/images/running-light-green.png" alt="" />
				<img v-else class="h-24 w-auto" src="@/assets/images/dumbbell-light-green.png" alt="" />
				<span class="mt-6 py-1.5 px-5 text-xs text-white bg-at-light-green rounded-lg shadow-md">
					{{ data.workout_type }}
				</span>
				<div class="w-full mt-6">
					<input v-if="edit" v-model="data.workout_name" type="text" class="p-2 w-full text-gray-500 focus:outline-none" />
					<h1 v-else class="text-at-light-green text-2xl text-center">
						{{ data.workout_name }}
					</h1>
				</div>
			</div>
			<!-- Exercises Info -->
			<div class="mt-10 p-8 rounded-md flex flex-col item-center bg-light grey shadow-md">
				<!-- Strength Trainig -->
				<div v-if="data.workout_type === 'strength'" class="flex flex-col gap-y-4 w-full">
					<div class="flex flex-row gap-x-6 gap-y-2 relative" v-for="(item, index) in data.exercises" :key="index">
						<div class="flex flex-2 flex-col md:w-1/3">
							<label id="exercise-name" for="exercise-name" class="mb-1 text-sm text-at-light-green">
								Exercise
							</label>
							<input id="exercise-name" v-if="edit" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.exercise" />
							<p v-else>{{ item.exercise }}</p>
						</div>
						<div class="flex flex-1 flex-col">
							<label id="sets" for="sets" class="mb-1 text-sm text-at-light-green">
								Sets
							</label>
							<input v-if="edit" id="sets" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.sets" />
							<p v-else>{{ item.sets }}</p>
						</div>
						<div class="flex flex-1 flex-col">
							<label id="reps" for="reps" class="mb-1 text-sm text-at-light-green">
								Reps
							</label>
							<input v-if="edit" id="reps" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.reps" />
							<p v-else>{{ item.reps }}</p>
						</div>
						<div class="flex flex-1 flex-col">
							<label id="weight" for="weight" class="mb-1 text-sm text-at-light-green">
								Weight (kg's)
							</label>
							<input v-if="edit" id="weight" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.weight" />
							<p v-else>{{ item.weight }}</p>
						</div>
						<img @click="deleteExercise(item.id)" v-if="edit" class="absolute h-4 w-auto -left-5 cursor-pointer" src="@/assets/images/trash-light-green.png" alt="" />
					</div>
					<button @click="addExercise" v-if="edit" type="button" class="mt-6 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green">Add Exercise</button>
				</div>
				<!-- Cardio Trainig -->
				<div v-else class="flex flex-col gap-y-4 w-full">
					<div class="flex flex-row gap-x-6 gap-y-2 relative" v-for="(item, index) in data.exercises" :key="index">
						<div class="flex flex-2 flex-col md:w-1/3">
							<label id="cardio-type" for="cardio-type" class="mb-1 text-sm text-at-light-green">
								Type
							</label>
							<select id="cardio-type" v-if="edit" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.cardioType">
								<option value="#">Select Type</option>
								<option value="Run">Run</option>
								<option value="Walk">Walk</option>
								<option value="Cycling">Cycling</option>
							</select>
							<p v-else>{{ item.cardioType }}</p>
						</div>
						<div class="flex flex-1 flex-col">
							<label id="distance" for="distance" class="mb-1 text-sm text-at-light-green">
								Distance
							</label>
							<!-- fix why box does not change the styling -->
							<input v-if="edit" id="distance" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.distance" />
							<p v-else>{{ item.distance }}</p>
						</div>
						<div class="flex flex-1 flex-col">
							<label id="duration" for="duration" class="mb-1 text-sm text-at-light-green">
								Duration
							</label>
							<input v-if="edit" id="duration" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.duration" />
							<p v-else>{{ item.duration }}</p>
						</div>
						<div class="flex flex-1 flex-col">
							<label id="pace" for="pace" class="mb-1 text-sm text-at-light-green">
								Pace
							</label>
							<input v-if="edit" id="pace" type="text" class="p-2 w-full text-gray-500 focus:outline-none" v-model="item.pace" />
							<p v-else>{{ item.pace }}</p>
						</div>
						<img @click="deleteExercise(item.id)" v-if="edit" class="absolute h-4 w-auto -left-5 cursor-pointer" src="@/assets/images/trash-light-green.png" alt="" />
					</div>
					<button @click="addExercise" v-if="edit" type="button" class="mt-6 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green">Add Exercise</button>
				</div>
			</div>
			<!-- Update -->
			<button v-if="edit" type="button" @click="update" class="mt-10 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green">Update</button>
		</div>
	</div>
</template>

<script>
import { ref, computed, inject } from "vue";
import { supabase } from "../supabase/init";
import { useRoute, useRouter } from "vue-router";
import { uid } from "uid";
export default {
	name: "view-workout",
	setup() {
		// Create data / vars
		const store = inject("store");
		const data = ref(null);
		const errorMsg = ref(null);
		const statusMsg = ref(null);
		const route = useRoute();
		const router = useRouter();
		const user = computed(() => store.state.user);
		const isLoading = ref(false);

		// Get current Id of route
		const currentId = route.params.workoutId;

		// Get workout data
		const getData = async () => {
			isLoading.value = true;
			try {
				const { data: workouts, error } = await supabase
					.from("workouts")
					.select("*")
					.eq("id", currentId)
					.eq("user_id", user.value.id);
				if (error) throw error;
				if (!workouts.length) {
					errorMsg.value = "Not found!";
					return;
				}
				data.value = workouts[0];
			} catch (error) {
				errorMsg.value = error.message;
				setTimeout(() => {
					errorMsg.value = false;
				}, 5000);
			}
			isLoading.value = false;
		};
		getData();

		// Delete workout
		const deleteWorkout = async () => {
			const confirmToDelete = confirm("Are you sure?");
			if (confirmToDelete) {
				isLoading.value = true;
				try {
					const { error } = await supabase
						.from("workouts")
						.delete()
						.eq("id", currentId)
						.eq("user_id", user.value.id);
					if (error) throw error;
					router.push({ name: "Home" });
				} catch (error) {
					errorMsg.value = `Error: ${error.message}`;
					setTimeout(() => {
						errorMsg.value = false;
					}, 5000);
				}
				isLoading.value = false;
			}
			return;
		};

		// Edit mode
		const edit = ref(null);

		const editMode = () => {
			edit.value = !edit.value;
		};

		// Add exercise
		const addExercise = () => {
			if (data.value.workoutType === "strength") {
				data.value.exercises.push({
					id: uid(),
					exercise: "",
					sets: "",
					reps: "",
					weight: "",
				});
				return;
			}
			data.value.exercises.push({
				id: uid(),
				cardioType: "",
				distance: "",
				duration: "",
				pace: "",
			});
		};

		// Delete exercise
		const deleteExercise = (id) => {
			if (data.value.exercises.length > 1) {
				data.value.exercises = data.value.exercises.filter((exercise) => exercise.id != id);
				return;
			}
			errorMsg.value = "Cannot remove! You need to have at least 1 exercise!";
			setTimeout(() => {
				errorMsg.value = false;
			}, 5000);
		};

		// Update Workout
		const update = async () => {
			isLoading.value = true;
			try {
				const { error } = await supabase
					.from("workouts")
					.update({
						workout_name: data.value.workout_name,
						exercises: data.value.exercises,
					})
					.eq("id", currentId)
					.eq("user_id", user.value.id);
				if (error) throw error;
				edit.value = false;
				statusMsg.value = "Workout updated!";
				setTimeout(() => {
					statusMsg.value = false;
				}, 5000);
			} catch (error) {
				errorMsg.value = `Error: ${error.message}`;
				setTimeout(() => {
					errorMsg.value = false;
				}, 5000);
			}
			isLoading.value = false;
		};

		return { statusMsg, data, errorMsg, edit, editMode, user, deleteWorkout, addExercise, deleteExercise, update, isLoading };
	},
};
</script>
