<template>
	<div class="max-w-screen-sm mx-auto px-4 py-10">
		<!-- error handling -->
		<div v-if="errorMsg" class="mb-10 p-4 rounded-md bg-light-grey shadow-lg">
			<p class="text-red-500">{{ errorMsg }}</p>
		</div>
		<!-- registration form -->
		<form @submit.prevent="register" class="p-8 flex flex-col bg-light-grey rounded-md shadow-lg">
			<h1 class="text-3xl text-at-light-green mb-4">Register</h1>
			<div class="flex flex-col mb-2">
				<label for="email" class="mb-1 text-sm text-at-light-green">E-mail</label>
				<input type="email" v-model="email" required name="" id="email" class="p-2 text-grey-500 focus:outline-none" />
			</div>
			<div class="flex flex-col mb-2">
				<label for="password" class="mb-1 text-sm text-at-light-green">Password</label>
				<input type="password" v-model="password" required name="" id="password" class="p-2 text-grey-500 focus:outline-none" />
			</div>
			<div class="flex flex-col mb-2">
				<label for="confirm-password" class="mb-1 text-sm text-at-light-green">Confirm Password</label>
				<input type="password" v-model="confirmPassword" required name="" id="confirm-password" class="p-2 text-grey-500 focus:outline-none" />
			</div>
			<button type="submit" class="mt-6 py-2 px-6 rounded-sm self-start text-sm text-white bg-at-light-green duration-200 border-solid border-2 border-transparent hover:border-at-light-green hover:bg-white hover:text-at-light-green">Register</button>
			<router-link class="text-sm mt-6 text-center" :to="{ name: 'Login' }">Already have an account? <span class="text-at-light-green">Login</span></router-link>
		</form>
	</div>
</template>

<script>
import { ref } from "vue";
import { supabase } from "../supabase/init";
import { useRouter } from "vue-router";

export default {
	name: "register",
	setup() {
		// Create data / vars
		const router = useRouter();
		const email = ref(null);
		const password = ref(null);
		const confirmPassword = ref(null);
		const errorMsg = ref(null);

		// Register function
		const register = async () => {
			if (password.value === confirmPassword.value) {
				try {
					const { error } = await supabase.auth.signUp({
						email: email.value,
						password: password.value,
					});
					if (error) throw error;
					router.push({ name: "Login" });
				} catch (error) {
					errorMsg.value = error.message;
					setTimeout(() => {
						errorMsg.value = null;
					}, 5000);
				}
				return;
			}
			errorMsg.value = "Passwords do not match!";
			setTimeout(() => {
				errorMsg.value = null;
			}, 5000);
		};

		return { email, password, confirmPassword, errorMsg, register };
	},
};
</script>
